body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-container {
    margin: 0 auto;
		width: 300px; 
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, 35%);
		        transform: translate(-50%, 35%);
}

.center {
  position: relative;
  top: 0; left: 0; bottom: 0; right: 0;
}

.content {
	float: left;
	width: 97%;
	margin: 0% 1.5% 1.5%;
	border-radius: 15px;
}

.content-jumbotron {
	width: 97%;
	margin: 0% 1.5% 0% 1.5%;
}

.no-padding {
	padding: 0 !important;
}

.top-bottom-padding {
	padding-top: .5em; 
	padding-bottom: .5em;	
}

.to-front {
	position: relative;
	z-index: 100;
}
.no-suggestions {
  color: #999;
  padding: 0.5rem;
}

.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  max-height: 11em;
  overflow-y: auto;
  padding-left: 0;
  width: calc(18.75em + 1rem);
  position:absolute;
  background-color: rgb(232,232,232);

}

.suggestions p {
  padding: 0.5rem;
  margin-bottom: 0px;
}

.suggestion-active,
.suggestions p:hover {
  background-color: #C0C0C0;
  color: #2f4f4f;
  cursor: pointer;
  font-weight: 700;
}

.suggestions p:not(:last-of-type) {
  border-bottom: 0.0625em solid #999;
}

.form-control {
  width: 20rem;
}

.rv-discrete-color-legend {
  box-sizing: border-box;
  overflow-y: auto; }
  .rv-discrete-color-legend.horizontal {
    white-space: nowrap; }

.rv-discrete-color-legend-item {
  color: #3a3a48;
  border-radius: 1px;
  padding: 9px 10px; }
  .rv-discrete-color-legend-item.horizontal {
    display: inline-block; }
    .rv-discrete-color-legend-item.horizontal .rv-discrete-color-legend-item__title {
      margin-left: 0;
      display: block; }

.rv-discrete-color-legend-item__color {
  display: inline-block;
  vertical-align: middle;
  overflow: visible; }

.rv-discrete-color-legend-item__color__path {
  stroke: #dcdcdc;
  stroke-width: 2px; }

.rv-discrete-color-legend-item__title {
  margin-left: 10px; }

.rv-discrete-color-legend-item.disabled {
  color: #b8b8b8; }

.rv-discrete-color-legend-item.clickable {
  cursor: pointer; }
  .rv-discrete-color-legend-item.clickable:hover {
    background: #f9f9f9; }

.rv-search-wrapper {
  display: flex;
  flex-direction: column; }

.rv-search-wrapper__form {
  flex: 0 1; }

.rv-search-wrapper__form__input {
  width: 100%;
  color: #a6a6a5;
  border: 1px solid #e5e5e4;
  padding: 7px 10px;
  font-size: 12px;
  box-sizing: border-box;
  border-radius: 2px;
  margin: 0 0 9px;
  outline: 0; }

.rv-search-wrapper__contents {
  flex: 1 1;
  overflow: auto; }

.rv-continuous-color-legend .rv-gradient {
  height: 40px;
  border-style: solid;
  border-width: 1px;
  border-color: black;
  margin-bottom: 5px; }

.rv-continuous-size-legend {
  font-size: 12px; }
  .rv-continuous-size-legend .rv-bubbles {
    text-align: justify;
    overflow: hidden;
    margin-bottom: 5px;
    width: 100%; }
  .rv-continuous-size-legend .rv-bubble {
    background: #d8d9dc;
    display: inline-block;
    vertical-align: bottom; }
  .rv-continuous-size-legend .rv-spacer {
    display: inline-block;
    font-size: 0;
    line-height: 0;
    width: 100%; }

.rv-legend-titles {
  height: 16px;
  position: relative; }

.rv-legend-titles__left,
.rv-legend-titles__right,
.rv-legend-titles__center {
  position: absolute;
  white-space: nowrap;
  overflow: hidden; }

.rv-legend-titles__center {
  display: block;
  text-align: center;
  width: 100%; }

.rv-legend-titles__right {
  right: 0; }

